.pay-btn{
  margin-top: 5px;
}
.pay-btn .homelogin{
  border: none;
  border-radius: 5px;
}
.input-border{
  border-radius: 5px;
  padding: 6px;
  border: 1px solid hsl(0deg 0% 84%);
  background-color: #fff;
}
.inputrow input::placeholder{
  color: #666;
}
.inputrow {
  /* width: 80%; */
  display: flex;
  flex-direction: column;

}
.payment-box{
    width: 710px; 
    max-width: 78%;
    /* height: 35%;*/
    /* background-color: #fff;*/
    padding: 0px;
    position:relative;
    top: 12%;
}

.image-size{
  width: 90%;
}
.payment-box h2{
   font-family: 'DM Sans', sans-serif;
  font-style: normal;
  font-weight: 700;
  font-size: 20px;
  line-height: 24px;
  letter-spacing: 0.15px;
  color: #0A0E0F;
}

.payment-box li{
  font-weight: 600;
  font-size: 14px;
  line-height: 24px;
  color: #4E4F51;
   font-family: 'DM Sans', sans-serif;
  list-style: none;
  padding: 0px;
}

.payment-box li::before{
  content: url(data:image/png;base64,);
  font-size: 20px;
  vertical-align: middle;
  padding-right: 6px;
}

.tip-center{
  padding-left: 0;
}
.flex-state{
  display: flex;
  justify-content: space-between;
}

.check-center{
  position: relative;
  top: 0rem;
  padding: 0 2rem;
}
.main-label label {
  margin: 0;
}

.main-label{
 display: flex;
 flex-direction: column;
 margin: 0 auto;
justify-content: center;
/* position: relative; */
/* left: 4%; */
width: 80%;
}

.tipping-check p{
margin-bottom: 0.5rem;
font-size: 14px;
}
.tipping-check h5{
  font-size: 20px;
  margin-bottom: 0.3rem;
  }

.checkout-tip{
  display: flex;
  align-items: center;
  color: #fff;
  width: 50%;
  border-radius: 20px;
  border: 0.5px solid #ffd9007f;
  background: rgba(0, 0, 0, 0.8);
  margin: 5px 5px 0px 5px;
  padding: 2px;
  height: 100%;
}
.returns{
  border-radius: 3px;
  display: flex;
  justify-content:right;
}
.returns span{
  font-size:1rem;
  font-weight: 700;
  letter-spacing: 0px;
  text-transform: none;
  color: #ffffff;
  background: #a55947;
  border-bottom: 1px solid #e97f65;
  padding: 6px 6px;
  margin: 5px 0;
  border-radius: 3px;
}
.tipping-list{
padding: 0;
margin-top: 4px;
}

.tipping-list li{
font-size: 11px;
font-weight: 450;
line-height: 17px;
list-style: none;
}

.tipping-list li i{
  color: #a55947;
  padding-right: 10px;
}

.center-image{
  /* position: relative; */
  /* bottom: 4.1rem; */
}

.container-pw{
  max-width: 100%;
}

.css-319lph-ValueContainer{
  height: 53px;
  margin-top: -5px
}
.sel-main {
  display: flex;
  padding: 8px 0px;
  grid-gap: 10px;
  align-items: center;
}
.sel-main p {
   font-family: 'DM Sans', sans-serif;
  font-style: normal;
  font-weight: 600;
  font-size: 16px;
  line-height: 24px;
  color: #4e4f51;
}
.wel-form {
  position: absolute;
  left: 0;
  padding: 0;
}
.atmcard .modal-content{
  background-color: #ccc;
}
.image-center{
  margin: auto;
}

.pack-heading{
  width: 210%;
    text-align: center;
  font-size: 2rem;
}

.box-container{
  display: flex;
  justify-content: center;
  flex-direction: column;
  flex-wrap: wrap;
}

.pack-main {
  background: url('../../Images/welcome-img.WebP');
  background-size: cover;
  background-position: center;
  background-repeat: no-repeat;
  background-size: cover;
  width: 100%;
  height: 100Vh;
  /* height: 100%; */
}
.css-6j8wv5-Input {
  height: 50px;
  border-radius: 15px;
}
section.package-sec {
  padding: 60px 0px;
}
.boxcontant{
  background-color: "white" !important;
}
.cardboxes ::placeholder{
  color: rgb(68, 66, 66);
}
.cardcard{
  width :100% !important;
  justify-content: space-evenly !important;
  flex-direction: row;
  
}

.tile-main{
  width:100%;
  line-height:1;
}
