body {
  margin: 0;

 font-family: 'DM Sans', sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
   font-family: 'DM Sans', sans-serif;

}
.error_msg {
	width: 370px;
	padding: 15px;
	margin: 5px 0;
	font-size: 14px;
	background-color: #f34646;
	color: white;
	border-radius: 5px;
	text-align: center;
}
.success_msg {
	width: 370px;
	padding: 15px;
	margin: 5px 0;
	font-size: 14px;
	background-color: #92fe9d ;
	color:rgba(0, 0, 0, 0.731);
	border-radius: 5px;
	text-align: center;
}
.page-link{
	background-color:#10867F;
	padding: 10px;
	margin: 0 5px;
	color: white;
	width: 100px;
	justify-content: center;
	text-align: center;
	border-radius: 10px;
	font-weight: 600;
}

Link{
	text-decoration: none;
}

.active{
	color:#10867F;
}